h1, h2, h3, h4, h5, h6, p, td, li, a {
	font-family: "Rubik";
	font-weight: 500;
}

h1 {
	font-size: 40px;
}

h2 {
	font-size: 30px;
}

h3 {
	font-size: 25px;
}

p {
	font-size: 16px;
}

.teaser__text {
	font-size: 16px;
	font-weight: 400;
	line-height: 28px;
}

.contact__name {
	font-size: 11px;
	font-weight: 500;
	line-height: 13px;
}

.contact__status {
	font-size: 8px;
	font-weight: 400;
	line-height: 9px;
}

.message__text {
