}
.icon {
	display: inline-block;
	font: normal normal normal 1em/1 'Nucleo';
	color: inherit;
	flex-shrink: 0;
	text-transform: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	&--sm { font-size: 12px; }
	&--md { font-size: 16px; }
	&--lg { font-size: 20px; }

}

.icon-arrow-left::before { content: '/ea02'; }
.icon-arrow-right::before { content: '/ea03'; }
.icon-menu::before { content: '/ea04'; }

$white: #FFFFFF;
$lightgrey-100: #FAFAFA;
$lightgrey-75: #F5F3F7;
$lightgrey-50: #FAFAFA;
$grey: #A39DA9;
$darkgrey: #979797;
$black: #6E5D7E;
$magenta: #E844FF;
$violet: #8739FF;
$gradient: linear-gradient(225deg, $magenta 0%, $magenta 0.01%, $violet 100%);
$shadowcolor: rgba(62, 39, 83, 0.251202);

* {
	box-sizing: border-box;
}

body {
	background-color: $white;
}
.chat-app {
	width: 100%;
	max-width: 100vh;
	min-height: 100vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 75px;
	position: relative;
	padding: 32px;
	overflow: hidden !important;

	&__phone {
		width: 247px;
		height: 505px;
		border: 10px solid $white;
		border-radius: 30px;
		flex-shrink: 0;
		background-color: $lightgrey-75;
		position: relative;
		box-shadow: 0px 30px 60px -10px $shadowcolor;

		&::before {
			content: "";
			width: 60%;
			height: 20px;
			border-radius: 0px 0px 14px 14px;
			background-color: $white;
			position: absolute; 
			top: 0;
			left: 50%;
			transform: translateX(-50%)
		}
	}
	
	&__teaser, .teaser {
		max-width: 445px;

		&__title {
			margin-bottom: 24px;
		}

		&__text {
			color: $grey;
		}

	}

	&::before, 
	&::after {
		content: "";
		position: absolute;
		max-width: calc(75% - 100px);
		width: 500px;
		height: 150%;
		background: $gradient;
		border-radius: 50vw;
		z-index: -1;
	}

	&::before {
		bottom: 100px;
		left: -25%;
	}
	
	&::after {
		top: 100px;
		right: -25%;
		opacity: 0.03
	}

}